.container--slider {
  width: 90%;
  padding: 0;
  max-width: 900px;
  margin: auto;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0 0 3px #8d1c3d, 0 15px 25px rgb(170, 170, 170);
  margin-bottom: 4rem;
  @media (min-width: 768px) {
    width: 95%;
    max-width: 1000px;
  }
}

.slider {
  height: 400px;
  &__section {
    width: 100%;
  }

  &__img {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__button {
    z-index: 25;
    position: absolute;
    width: 40px;
    height: 40px;
    background: rgba(255, 255, 255, 0.7);
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-family: monospace;
    font-weight: bold;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.25s, color 0.25s;
  }
  &-theme {
    background-color: #8d1c3d;
  }
  &__button {
    &:hover {
      background-color: #fff;
      color: #8d1c3d;
    }

    &--next {
      right: 10px;
    }

    &--prev {
      left: 10px;
    }

    &-icon {
      font-size: 1.2rem;
    }
    @media (min-width: 768px) {
      height: 520px;
    }
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes removeBorder {
  to {
    border-right: 0px solid;
  }
}
