.header {
  position: fixed;
  z-index: 25;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background-color: var(--primary-shadow-color);
}

.nav {
  height: 100%;
}

.nav--footer {
  --bs-nav-link-padding-x: 1rem;
  --bs-nav-link-padding-y: 0.5rem;
  --bs-nav-link-font-weight: ;
  --bs-nav-link-color: var(--bs-link-color);
  --bs-nav-link-hover-color: var(--bs-link-hover-color);
  --bs-nav-link-disabled-color: #6c757d;
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 0;
  height: auto;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav__link-logo {
  object-fit: contain;
  display: block;
  width: 3rem;
  border-radius: 50%;
  height: 3rem;
}

.footer {
  background-color: var(--primary-highlight-dark-color);
  &__title {
    position: relative;
    font-size: 0.75rem;
    margin: 0;
    text-align: center;
    &::before {
      content: "";
      position: absolute;
      top: -0.5rem;
      left: 0;
      background-color: var(--reverse-primary-shadow-color);
      width: 100%;
      height: 0.0625rem;
    }
    @media (min-width: 768px) {
      font-size: 0.875rem;
    }
  }

  &__link {
    color: var(--primary-shadow-color);
    text-decoration: none;
  }
}
