.hero {
  width: 100%;
  min-height: 100vh;
  background-image: url("/assets/img/banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .hero::before {
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    opacity: 0.4;
    background-color: #000;
  }

  .hero__info {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    order: 0;
    @media (min-width: 1024px) {
      align-items: flex-end;
      padding: 0 5rem;
      order: 1;
      gap: 3rem;
    }
  }
}

.hero__title {
  margin: 0;
  font-weight: 900;
  color: var(--reverse-primary-text-color);
  text-align: center;
  @media (min-width: 1024px) {
    text-align: end;
    font-size: 4rem;
  }
}

.hero__text {
  width: 90%;
  text-align: center;
  margin: 0;
  @media (min-width: 1024px) {
    text-align: end;
    width: 50%;
    font-size: 1.3rem;
  }
}

.hero__img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: var(--primary-theme-color);
  &-container {
    order: 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (min-width: 768px) {
      order: 0;
    }
  }
  @media (min-width: 768px) {
    width: 15rem;
    height: 15rem;
  }
}
